// Utilizing the BEM methodology due to the disabling of shadow DOM for this component.

.dark-overlay {
  width: 100%;
  height: 100%;
  color: $white;
  background-color: $gray-50;

  .dark-overlay__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: rem(1296);
    height: 100%;
    margin: 0 auto;
  }

  .dark-overlay__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: rem(16) 0;
    font-size: 1.5rem;
    color: $white;

    @include mq('max', 'lg') {
      @media (orientation: landscape) {
        @include z-index('header');
        position: absolute;
        top: 0;
        left: 0;
        box-sizing: border-box;
        max-width: 100% !important;
        padding: rem(10) rem(20);
        margin: 0 !important;
        background-color: rgba($color: $white, $alpha: 0.1);
      }
    }

    @include mq('min', 'lg') {
      position: static;
      margin: rem(32) 0;
    }
  }

  .dark-overlay__brand-icon {
    font-size: rem(45);

    @include mq('max', 'md') {
      font-size: 2rem;
    }
  }

  .dark-overlay__close-icon {
    padding: rem(6);
    font-size: rem(20);
    color: currentColor;
    background-color: $gray-200;
    border: 0;
    border-radius: 50%;
    outline: none;

    @include mq('min', 'md') {
      padding: rem(8);
      font-size: rem(24);
    }

    &:focus {
      background-color: $gray-400;
    }
  }

  .dark-overlay__navigation-icon {
    position: absolute;
    top: 50%;
    padding: rem(15);
    font-size: rem(24);
    color: currentColor;
    color: $white;
    background-color: $gray-200;
    border: 0;
    border-radius: 50%;
    @include z-index('base');
    outline: none;

    &:focus {
      background-color: $gray-400;
    }

    &--previous {
      left: rem(-12);
      transform: rotate(180deg) translateY(50%);
      transform-origin: left center;
    }

    &--next {
      right: rem(-12);
      transform: translateY(-50%) translateX(100%);
    }

    @include mq('max', 'xl') {
      display: none;
    }
  }

  .dark-overlay__body {
    position: relative;
    flex: 1 1 500px;
    min-height: 1px;

    @include mq('max', 'lg') {
      display: flex;
      align-items: center;
      width: 100%;
      overflow: scroll;
    }

    @include mq('min', 'xl') {
      flex: 0 1 679px;
    }
  }

  .dark-overlay__footer {
    display: flex;
    gap: rem(20);
    align-items: flex-end;
    justify-content: space-between;
    margin: rem(16) 0;
    color: $white;

    @include mq('max', 'lg') {
      @media (orientation: landscape) {
        @include z-index('header');
        position: absolute;
        bottom: 0;
        left: 0;
        box-sizing: border-box;
        max-width: 100% !important;
        padding: rem(10) rem(20);
        margin: 0 !important;
        background-color: rgba($color: $white, $alpha: 0.1);
      }
    }

    @include mq('min', 'lg') {
      box-sizing: border-box;
      gap: rem(80);
      align-items: center;
      margin: rem(66) 0 rem(32);
    }
  }

  .dark-overlay__title {
    @include font('body-default');
  }

  .dark-overlay__logo {
    max-width: 120px;
    max-height: 25px;

    @include mq('min', 'md') {
      max-width: 200px;
      max-height: 55px;
    }
  }
}

.media-container {
  width: 100%;
  height: 100%;
}

.footer-main {
  display: flex;
  flex: 1;
  flex-direction: column;
  gap: rem(14);
}
