:host .youtube-control {
  background-color: $gray-200;
  border-radius: 5px;

  @include mq('max', 'md') {
    width: 100%;
  }
}

:host .media-control-bar {
  display: flex;
  gap: rem(8);
  align-items: center;
  height: 40px;
  padding: 0 8px;

  color: $white;

  @include mq('max', 'md') {
    gap: 1rem;
    height: 54px;
  }
}

:host .youtube-control-button {
  font-size: rem(16);
  color: $white;
  background: transparent;
  border: 0;
  outline-color: $white;

  @include mq('max', 'md') {
    font-size: rem(19);
  }
}

:host .slider {
  width: 100%;
  height: 3px;
  appearance: none;
  outline-color: $white;

  &::-webkit-slider-thumb {
    width: 12px;
    height: 12px;
    appearance: none;
    cursor: grabbing;
    background: $primary;
    border: 2px solid $white;
    border-radius: 50%;
  }

  &:disabled {
    background-color: $gray-400;

    &::-webkit-slider-thumb {
      cursor: default;
    }
  }
}

:host bs-button {
  font-size: font-size(large);
  line-height: 0;
}

:host bs-icon {
  font-size: rem(24);
}
