import { Component, Event, EventEmitter, Host, Listen, Prop, State, h } from '@stencil/core';
import { isEmpty } from '@springbokagency/stencil-base';
import {
  PlayerState,
  YoutubeControlUpdateEvent,
} from '../../organisms/youtube-video/youtube-video.types';
import { IconName } from '../../atoms/icon/icon.types';
import { BsClickActionCustomEvent, ClickActionEvent } from '../../../components';
import { YoutubeControlClickEvent } from './youtube-control.types';

/**
 * Youtube controls component.
 */
@Component({
  tag: 'bs-youtube-control',
  styleUrl: 'youtube-control.scss',
  shadow: true,
})
export class BsYoutubeControl {
  @State() videoActive: boolean = false;

  /** Props */
  /** The video ID the controller belongs to. */
  @Prop() videoId!: string;

  /** The playing state of the youtube Player. */
  @Prop() playerState?: PlayerState = PlayerState.UNSTARTED;

  /** Is the video muted. */
  @Prop() mute?: boolean = true;

  /** The video current progress time. */
  @Prop() currentTime?: number = 0;

  /** Events */
  /**
   * Youtube control event
   */
  @Event() bsYoutubeControllerClick: EventEmitter<YoutubeControlClickEvent>;

  /** Listeners */
  @Listen('bsYoutubeControllerUpdate', { target: 'window' })
  bsYoutubeControllerUpdateHandler(e: CustomEvent<YoutubeControlUpdateEvent>) {
    if (this.videoId === e.detail.id) {
      this.mute = e.detail.mute;
      this.playerState = e.detail.playerState;
      this.currentTime = isEmpty(e.detail.currentTime) ? 0 : e.detail.currentTime;
      this.videoActive = [PlayerState.PLAYING, PlayerState.PAUSED, PlayerState.BUFFERING].includes(
        e.detail.playerState
      );
    }
  }

  /** Methods */
  private onButtonClick = (e: BsClickActionCustomEvent<ClickActionEvent>) => {
    this.bsYoutubeControllerClick.emit({
      id: this.videoId,
      action: e.detail.name,
      value: e.detail.value,
    });
  };

  private onSliderChange = (e: InputEvent) => {
    const el = e.target as HTMLInputElement;

    this.bsYoutubeControllerClick.emit({
      id: this.videoId,
      action: 'progress-bar-input',
      value: el.valueAsNumber,
    });
  };

  render() {
    return (
      <Host>
        <div class="youtube-control">
          <div class="media-control-bar">
            <bs-click-action
              name="play"
              tagClass="youtube-control-button"
              onBsClickAction={this.onButtonClick}
              value={
                this.playerState === PlayerState.PLAYING ? PlayerState.PAUSED : PlayerState.PLAYING
              }
            >
              <bs-icon
                name={
                  [PlayerState.PLAYING, PlayerState.BUFFERING].includes(this.playerState)
                    ? IconName.Pause
                    : IconName.Play
                }
              />
            </bs-click-action>
            <input
              onInput={this.onSliderChange}
              value={this.currentTime}
              type="range"
              id="vol"
              name="vol"
              min="0"
              max="100"
              class="slider"
              step="0.1"
              disabled={!this.videoActive}
            ></input>
            <bs-click-action
              name="mute"
              tagClass="youtube-control-button"
              onBsClickAction={(e) => {
                this.mute = !this.mute;
                this.onButtonClick(e);
              }}
            >
              <bs-icon name={this.mute ? IconName.Mute : IconName.Volume} />
            </bs-click-action>

            <bs-click-action
              name="fullscreen"
              tagClass="youtube-control-button"
              onBsClickAction={this.onButtonClick}
              disabled={!this.videoActive}
            >
              <bs-icon name={IconName.Resize} />
            </bs-click-action>
          </div>
        </div>
      </Host>
    );
  }
}
