import { Component, Host, Listen, Method, Prop, State, h } from '@stencil/core';
import { Memoize, isEmpty, parseIfString } from '@springbokagency/stencil-base';
import { IconName } from '../../atoms/icon/icon.types';
import { BaseModalId } from '../base-modal/base-modal.types';
import { BsImage } from '../../atoms/image/image';
import { Breakpoint, compareBreakpoint } from '../../../utils/breakpoints';
import { throttle } from '../../../utils/throttle';

/**
 * Dark-theme overlay component.
 */
@Component({
  tag: 'bs-dark-overlay',
  styleUrl: 'dark-theme-overlay.scss',
  shadow: false, // no shadow DOM to make focus-trap work with slots
})
export class BsDarkOverlay {
  private modalRef: HTMLBsBaseModalElement;

  /** States */
  @State() isMobile: boolean = compareBreakpoint('max', Breakpoint.lg);

  /** Props */
  /**
   * The title of the carousel displayed in the footer.
   */
  @Prop() mainTitle?: string;

  /**
   * Does the content contain multiple screens?
   */
  @Prop() screens?: boolean = false;

  /**
   * The logo to be displayed in the footer.
   */
  @Prop() logo: BsImage | string;

  @Memoize('logo')
  get parsedLogo(): BsImage {
    return parseIfString(this.logo);
  }

  /** Listeners */
  @Listen('resize', { target: 'window' })
  handleScreenResized() {
    this.throttleMethod();
  }

  /** Methods */
  private onResize = () => {
    this.isMobile = compareBreakpoint('max', Breakpoint.lg);
  };

  private throttleMethod = throttle(this.onResize, 1000);

  /**
   * This method triggers the close method on the modal.
   */
  @Method()
  async closeOverlay() {
    this.modalRef.close();
  }

  /**
   * This method triggers the previous slide method on the modal.
   */
  @Method()
  async previousSlide() {
    this.modalRef.previousSlide();
  }

  /**
   * This method triggers the next slide method on the modal.
   */
  @Method()
  async nextSlide() {
    this.modalRef.nextSlide();
  }

  /**
   * This method triggers the handle focus method on the modal.
   */
  @Method()
  async focusTrap() {
    this.modalRef.handleFocus();
  }

  render() {
    return (
      <Host>
        <bs-base-modal
          ref={(el) => {
            this.modalRef = el;
          }}
          modalId={BaseModalId.Media}
          transitionType="fade"
        >
          <div class="dark-overlay">
            <div class="dark-overlay__content">
              {/* Head */}
              <bs-container class="dark-overlay__head" contained={true}>
                <bs-icon class="dark-overlay__brand-icon" name={IconName.BMark} />
                <bs-click-action
                  name="close-dark-overlay"
                  tagClass="dark-overlay__close-icon"
                  onBsClickAction={this.closeOverlay.bind(this)}
                >
                  <bs-icon name={IconName.Close} />
                </bs-click-action>
              </bs-container>

              {/* Body */}
              <bs-container class="dark-overlay__body" mobileFullWidth={true} contained={true}>
                {this.screens && (
                  <bs-click-action
                    name="previous-media"
                    tagClass="dark-overlay__navigation-icon dark-overlay__navigation-icon--previous"
                    onBsClickAction={this.previousSlide.bind(this)}
                    disabled={this.isMobile}
                  >
                    <bs-icon name={IconName.CaretForward} />
                  </bs-click-action>
                )}
                <div class="media-container">
                  <slot name="media"></slot>
                </div>
                {this.screens && (
                  <bs-click-action
                    name="next-media"
                    tagClass="dark-overlay__navigation-icon dark-overlay__navigation-icon--next"
                    onBsClickAction={this.nextSlide.bind(this)}
                    disabled={this.isMobile}
                  >
                    <bs-icon name={IconName.CaretForward} />
                  </bs-click-action>
                )}
              </bs-container>

              {/* Footer */}
              <bs-container class="dark-overlay__footer" contained={true}>
                <div class="footer-main">
                  {!isEmpty(this.mainTitle) && (
                    <bs-text text={this.mainTitle} class="dark-overlay__title" />
                  )}
                  <slot name="controller" />
                </div>
                {!isEmpty(this.parsedLogo) && (
                  <bs-image
                    class="dark-overlay__logo"
                    src={this.parsedLogo.src}
                    alt-text={this.parsedLogo.altText}
                  />
                )}
              </bs-container>
            </div>
          </div>
        </bs-base-modal>
      </Host>
    );
  }
}
